import React from "react";
import Reward from 'react-rewards';
import styles from './blog.module.css';
import DisplayBlogPost from "./blogDisplay";
import Loading from '../loading/loading';



/**
 * This will be a daily/weekly/monthly blog area. Each post may contain
 * content, image, location
 * TODO:
 * - Have notes/text area in the right side where we'll be typing live notes down
*/
class Blog extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            addBtn: false,
            loading: false,
            posts:[{
                content: 'default content',
                location: 'default location',
                date: new Date(),
            }]
        }

        this.displayLoading = this.displayLoading.bind(this);
        this.removeDisplayLoading = this.removeDisplayLoading.bind(this);
    }

    componentDidMount(){
        let posts = [{
            content: "I went to Costco with my house mates today",
            location: "San Diego",
            date: "2021 - 09 - 02T03: 11: 48.194+00: 00"
        }, {
            content: "Today I finished most of the website components",
            location: "San Diego",
            date: "2021 - 08 - 30T06: 30: 03.572 + 00: 00"
        }, {
            content: "Got my new Samsung S20 FE phone today",
            location:  "Garden Grove",
            date: "2021 - 09 - 04T23: 08: 19.024+00: 00"
        }, {
            content: "Had a good talk with my housemates.",
            location: "San Diego",
            date:  "2021 - 09 - 08T08: 53: 50.677+00: 00"
        }, {
            content: "It was fun! Had Hotpot with family!",
            location: "Garden Grove",
            date: "2021 - 09 - 14T05: 19: 38.910+00: 00"
        }]
        this.setState({
            ...this.state,
            loading: true
        })
        // GET blog document in mongo
        setTimeout(async () => {
            let counter = 1;
            posts.forEach((post) => {
                this.setState({
                    addBtn: false,
                    loading: false,
                    posts: [...this.state.posts, {
                        content: post.content,
                        location: post.location,
                        image: post.image,
                        index: counter++,
                        date: post.date,
                    }]
                })
            })
            this.reward.rewardMe();
        })
    }


    


    /**
     * Mount loading component to parent component
     * @returns Loading component
     */
    displayLoading() {
        if (this.state.loading) {
            return <Loading loading={this.state.loading} />
        }
    }


    /**
     * Set state for loading when removing goal
     */
    removeDisplayLoading() {
        this.setState({
            ...this.state,
            loading: true
        })
        this.reward.rewardMe();
    }

    render(){
        return (
            <div className={styles.page}>
                <div className={styles['reward']} id="reward-container">
                    <Reward
                        ref={(ref) => { this.reward = ref }}
                        type='emoji'
                        config={{
                            zIndex: 10000,
                            spread: 180
                        }}
                    >
                        <p id="reward"></p>
                    </Reward>
                </div>
                <h1 className={styles["header"]} id="blog-header">My Blog</h1>
                {
                    this.displayLoading()
                }
                <div className="container" id={styles["post-container"]}>
                    {
                        this.state.posts.slice(1).map((post) =>{
                            // console.log(post);
                            return <DisplayBlogPost key={"post-"+post.index} content={post.content} location={post.location} image={post.image} date={post.date} index={post.index} removeDisplayLoading={this.removeDisplayLoading} isAdmin={this.props.isAdmin}/>
                        })
                    }
                </div>
            </div>
        )
    }
};

export default Blog;