import React, { useState, useEffect } from "react";
import Reward from 'react-rewards';
import styles from './goals.module.css';
import GoalInterface from "./goalsPostInterface";
import GoalDisplay from './goalsDisplay';
import Loading from '../loading/loading';

/**
 * Goals page component
 */
class Goals extends React.Component {
    // const [removeInterface, setInterfaceRemove ] = useState(false);
    constructor(props){
        super(props)

        this.state = {
            //store an array of goals, each goals have its own content and tag color (div color)
            goals:[{
                content: '',
                tagColor: '#FFFFFF',
                date: new Date(),
                index: 0
            }],
            loading: false,
            removeInterface: true
        }
        this.displayAddBtn = this.displayAddBtn.bind(this);
        this.displayLoading = this.displayLoading.bind(this);
        this.removeDisplayLoading = this.removeDisplayLoading.bind(this);
    }

    /**
     * Get goals from database and set state for display
     */
    componentDidMount(){
        this.setState({
            ...this.state,
            loading: true
        })
        setTimeout(async () => {
            let arrGoal = [{
                content: "Start working on making a small co-op game",
                tagColor: "#ff8800",
                date: "2021 - 09 - 02T03: 17: 36.052+00: 00"
            }, {
                content: "Apply to more internships on Handshake and Linkedin",
                tagColor: "#000000",
                date: "2021 - 09 - 02T03: 18: 17.343+00: 00"
            }, {
                content: "Finished General Resume",
                tagColor: "#05c1ff",
                date: "2021-09-05T01:11:14.593+00:00"
            }, {
                content: "Learn some advance course of CSS",
                tagColor: "#7300ff",
                date: "2021 - 09 - 07T02: 21: 53.140+00: 00"
            }, {
                content: "Successfully pass my interview",
                tagColor: "#ff0000",
                date: "2021 - 09 - 08T08: 57: 41.353+00: 00"
            }, {
                content: "start working on other app",
                tagColor: "#ffae00",
                date: "2021 - 09 - 10T03: 15: 48.676+00: 00 "
            }, {
                content: "Get ready for Junior year of college",
                tagColor: "#00ff08",
                date: "2021 - 09 - 14T05: 28: 26.454+00: 00"
            }, {
                content: "Have fun with coding!",
                tagColor: "#ff0066",
                date: "2021 - 09 - 18T00: 21: 32.070+00: 00"
            }];
            let goalsCount = 1;
            arrGoal.forEach((post) => {
                this.setState({
                    ...this.state,
                    loading: false,
                    goals:[...this.state.goals, {
                        content: post.content,
                        tagColor: post.tagColor,
                        date: post.date,
                        index: goalsCount++
                    }]
                })
            })
            if (!this.props.isAdmin) {
                console.log("Not an Admin!");
                document.getElementById('add-goals-btn').style.visibility = 'none';
            } else {
                document.getElementById('add-goals-btn').style.visibility = "visible";
                console.log("I AM ADMIN!");
            }
            this.reward.rewardMe();
        }, 100)
    }


    /**
     * Mount loading component to parent component
     * @returns Loading component
     */
    displayLoading() {
        if (this.state.loading) {
            return <Loading loading={this.state.loading} />
        }
    }

    /**
     * Set state for loading when removing goal
     */
    removeDisplayLoading(){
        this.setState({
            ...this.state,
            loading: true
        })
    }

    displayAddBtn(){
        if (this.props.isAdmin) {
            return <button type="button" className="add-interface-btn" id="add-goals-btn" onClick={this.displayInterface}>Add Goals</button>;
        } else {
            console.log("Not an Admin")
        }
    }

    render() {
        return (
            <div className={styles.page}>
                <div className={styles['reward']} id="reward-container">
                    <Reward
                        ref={(ref) => { this.reward = ref }}
                        type='emoji'
                        config={{
                            zIndex: 10000,
                            spread: 180
                        }}
                    >
                        <p id="reward"></p>
                    </Reward>
                </div>
                <h1 className={styles['header']} id="goals-header">Goals</h1>
                <button type="button" className={styles['add-btn']} id="add-goals-btn" onClick={this.displayInterface}>Add Goals</button>
                {
                    this.displayLoading()
                }
                <div className={styles["goals-container"]} id="goals-container">
                    {
                        this.state.goals.slice(1).map((goal) => {
                            // console.log('parent state index: ' + goal.index)
                            return <GoalDisplay key={"goal-" + goal.index} afterRemovalDisplay={this.afterRemovalDisplay} content={goal.content} 
                                tagColor={goal.tagColor} index={goal.index} date={goal.date} removeDisplayLoading={this.removeDisplayLoading} 
                                    updateGoalDisplay={this.updateGoalDisplay} isAdmin={this.props.isAdmin}/>
                        })
                    }
                </div>
            </div>
        )
    }
};

export default Goals;