import React from "react";
import styles from './resume.module.css';
import Reward from 'react-rewards';
import Confetti from './confetti';
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';
// import resumePDF from './resume.pdf';
// import DisplayResume from "./pdf.component";
import Me from './Me.jpg';
import NewResume from "./new_resume.jpg";
import LinkedinGif from "./linkedin_gif.gif";
import GithubGif from "./github_gif.gif";
import HandshakeIcon from "./handshake_icon.png";

const Linkedin = <a href="https://www.linkedin.com/in/trai-pham-4a1272198/" ><img src={LinkedinGif} width="40px" height="40px" />   Linkedin</a>
const GitHub = <a href="https://github.com/traipham"><img src={GithubGif} width="40px" height="40px" />   GitHub</a>
const HandShake = <a href="https://ucsd.joinhandshake.com/users/18070890"><img id={styles["handshake_icon"]} src={HandshakeIcon} width="40px" height="40px" />   HandShake</a>
/**
 * Name of Component: Resume
 * 
 * Description: This component is a page that would show my resume. I will be able to UPLOAD a new resume 
 * replacing the current one and DOWNLOAD the current resume.
 */
class Resume extends React.Component {
    constructor(props){
        super(props)

        this.state = {
            focus: false
        }

        this.focusFunc = this.focusFunc.bind(this);
    }


    componentDidUpdate(){
        window.location.reload();
    }

    focusFunc(){
        this.setState({
            focus: true
        })
    }

    render(){
        return (
            <div className={styles.page}>
                {/* <Confetti /> */}
                <div className={styles['reward']} id="reward-container">
                    <Reward
                        ref={(ref) => { this.reward = ref }}
                        type='emoji'
                        config={{
                            zIndex: 10000,
                            spread: 180
                        }}
                    >
                        <p id="reward"></p>
                    </Reward>
                </div>
                <h1 className={styles["header"]}>About Me</h1>
                <div className={styles['page-container']}>
                    <h1 style={{textAlign:"center"}}>Hi! My name is Trai Pham.</h1>
                    <h3>Personal Description</h3>
                    <div  id={styles["img-desc}"]}>
                        <div className={styles['img-container']}>
                            <img src={Me} width='300px' height='400px' />
                        </div>
                        <div id={styles["gradient-border"]}>
                            <div id={styles["my-description-container"]}>
                                <p id={styles["my-description"]} style={{"text-indent": "40px"}}>
                                        I'm an active student who had his fair share of different types of experiences and I'm hoping to have many more. I explored many
                                    different types of clubs at UCSD ranging from club rugby to KP. I'm a flexible team player and an aggressively
                                    determined individual. I'm always willing to learn anything new and would give my best effort in everything that I do. I
                                    hope to contribute to society as much as possible, and at the same time create something that I would be proud of.
                                    <br/>
                                    <br/>
                                    My goal is to become a software engineer after college. Software engineering sounds very challenging, but at the same time
                                    fun. I've taken many course pertaining to software engineering such as CSE 110 and I'm a part of many clubs(ACM) that encourages the
                                    idea of creation and education. I've also personally looked into many tools and techniques that software engineers would use
                                    such as JavaScript, Node.js, React.js, MongoDB, CRUD, REST api, and many more. I've created my own personal website using
                                    React.js, and I've been in a team where we built a bullet journal web-app from JavaScript, HTML, and CSS. I aspire to be
                                    a great software engineer, but I'm also determined to excel in any other types of role given to me. My end goal is to become
                                    a project manager of a software engineering team.
                                    <br />
                                    <br />
                                    Aside from school, I enjoy working out, hiking, playing games, hanging out with friends, and watching anime. As an individual,
                                    I wish to have a balance between my work-life/school-life and my personal-life. I hope to be successful, but at the same time
                                    content with myself. I want to be proud of what I'm doing and who I'm doing it for. Life is just beginning, and I hope to be able
                                    to experience everything that it has to offer.
                                    <br />
                                    <br />
                                        WELCOME TO MY WEBPAGE!
                                </p>
                            </div>
                        </div>
                    </div>
                    <br/>
                    <br/>
                    <h3>Contact Info</h3>
                    {Linkedin}<br /><br />
                    {GitHub}<br /><br />
                    {HandShake}
                    <hr id="h-line"></hr>
                    <div className={styles.container} id='container'>
                        <h2 id={styles['resume-header']}>Resume</h2>
                        <img src={NewResume} id={styles['resumePdf']} alt="resume" width="750px" height="980px" />
                        {/* <DisplayResume/> */}
                        <button type='button' className={styles['focus-btn']} id='focus-btn' onClick={this.focusFunc}>Focus</button>
                        <p>(Zoom in and click focus to see pdf clearer)</p>
                    </div>
                    <button type="button" className="upload-btn" >Upload</button>
                    <button type="button" className="download-btn">Download</button>
                </div>
            </div>
        )
    }
}

export default Resume;